export const rightAnimation = () => {
  const rhsObserver = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        entry.target.classList.add("show");
      } else {
        entry.target.classList.remove("show");
      }
    });
  });

  const rightenElements = document.querySelectorAll(".rightAnimation");
  rightenElements?.forEach((el) => rhsObserver.observe(el));
};

export const bottomAnimation = () => {
  const botObserver = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        entry.target.classList.add("show");
      } else {
        entry.target.classList.remove("show");
      }
    });
  });

  const bottomElements = document.querySelectorAll(".bottomAnimation");
  bottomElements?.forEach((el) => botObserver.observe(el));
};

